import React, { useState, useContext } from 'react'
import { Link } from 'react-router-dom'
import { AppContext } from './ContextProvider'
import 'font-awesome/css/font-awesome.min.css'
import './NewNavMenu-2.css'

const NewNavMenu2 = () => {

    const { userIsLogged, loggedIn } = useContext(AppContext)
    const [isCollapsed, setIsCollapsed] = useState(false)
    const [isOpen, setIsOpen] = useState(false)

    

    const toggleNavbar = (flag) => {
        setIsCollapsed(flag );
        const siteNavigation = document.getElementsByClassName('site-nagivation')[0]
        flag ? siteNavigation.classList.add('show-nav') : siteNavigation.classList.remove('show-nav')

        const linkNavigation = document.getElementsByClassName('ul-level-2')[0]
        if (isOpen) linkNavigation.classList.remove('show-link')
        setIsOpen(false)
    }
    
    const toggleLinkBar = (flag) => {
        console.log(flag)
        setIsOpen(flag)
        const linkNavigation = document.getElementsByClassName('ul-level-2')[0]
        flag ? linkNavigation.classList.add('show-link') : linkNavigation.classList.remove('show-link')
    }
    const toggleLogout = () => {
        loggedIn(false)
        toggleNavbar()
    }

    return (
        <header className="menu-bg">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 header-items">
                        <div><img className="logo" src="logo.jpg" alt="Company logo" /></div>
                        <div className="hambarger-menu" onClick={() => toggleNavbar(!isCollapsed)}>
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                        <nav className="site-nagivation">
                            <ul>
                                <li><Link to="/" onClick={() => toggleNavbar(!isCollapsed)}>Home</Link></li>
                                <li><Link to="/about" onClick={() => toggleNavbar(!isCollapsed)}>About</Link></li>
                                <li><Link to="/projects" onClick={() => toggleNavbar(!isCollapsed)} >Projects</Link></li>
                                <li><Link to="/gallery" onClick={() => toggleNavbar(!isCollapsed)}>Gallery</Link></li>
                                <li><Link to="/news" onClick={() => toggleNavbar(!isCollapsed)}>News</Link></li>
                                <li><Link to="/contact-us" onClick={() => toggleNavbar(!isCollapsed)}>Contact Us</Link></li>
                                {
                                    userIsLogged
                                        ?
                                        <li className="li-level-2">
                                            <div onClick={() => toggleLinkBar(!isOpen)}>
                                                <Link className="sub-link" to='/volunteer'>Volunteer</Link>
                                                <i className="fa fa-arrow-down" aria-hidden="true"></i>
                                            </div>
                                            <ul className="ul-level-2">
                                                <li><Link className="sub-link" to='/member-register' onClick={() => toggleNavbar(!isCollapsed)}>Member Registration</Link></li>
                                                {/*<li><Link className="sub-link" to='/' onClick={() => loggedIn(false)}>Logout</Link></li>*/}
                                                <li><Link className="sub-link" to='/' onClick={toggleLogout}>Logout</Link></li>
                                            </ul>
                                        </li>
                                        :
                                        <li><Link to="/login" onClick={() => toggleNavbar(!isCollapsed)}>Login</Link></li>
                                }
                            </ul>
                        </nav>
                    </div>
                </div>
            </div>
        </header>

    );
}

export default NewNavMenu2